
body {
  font-family: "微软雅黑" !important;
}
.Seller {
  background-color: rgba(7, 17, 27, 0.1);
  position: fixed;
  top: 175px;
  bottom: 0;
  left: 0;
  right: 0;
  box-sizing: border-box;
  width: 100%;
  overflow: hidden;
}
.sellerTop {
  padding: 0.48rem 0.48rem 0.48rem 0.48rem;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  background-color: #fff;
}
.sellerTop .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  padding: 0rem 0rem 0.48rem 0rem;
}
.sellerTop .title .title-left span,
.sellerTop .title .title-left em {
  line-height: 0.48rem;
  font-size: 0.26666667rem;
  color: #4d555d;
}
.sellerTop .title .title-left span {
  margin: 0rem 0.32rem 0rem 0rem;
}
.sellerTop .title h3 {
  font-size: 0.37333333rem;
  color: #07111b;
  line-height: 0.37333333rem;
  margin: 0rem 0rem 0.21333333rem 0rem;
}
.sellerTop .title div:last-child {
  text-align: center;
}
.sellerTop .title .comments {
  display: inline-block;
  margin: 0rem 0.21333333rem 0rem 0rem;
}
.sellerTop .delivery {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0.48rem 0rem 0rem 0rem;
}
.sellerTop .delivery li {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  font-size: 0.26666667rem;
  border-right: 1px solid rgba(7, 17, 27, 0.1);
}
.sellerTop .delivery li:last-child {
  border-right: none;
}
.sellerTop .delivery li p {
  color: #93999f;
  line-height: 0.26666667rem;
  margin: 0rem 0rem 0.10666667rem 0rem;
}
.sellerTop .delivery li strong {
  font-size: 0.64rem;
  font-weight: 200;
  color: #07111b;
  line-height: 0.64rem;
}
.activity {
  padding: 0.48rem 0.48rem 0rem 0.48rem;
  background-color: #fff;
  margin: 0.48rem 0rem 0rem 0rem;
}
.activity h3 {
  margin: 0rem 0rem 0.21333333rem 0rem;
}
.activity p {
  padding: 0rem 0.32rem 0rem 0.32rem;
  font-size: 0.32rem;
  font-weight: 200;
  color: #f01414;
  line-height: 0.64rem;
  margin: 0rem 0rem 0.48rem 0rem;
}
.activity li {
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  height: 0.42666667rem;
  padding: 0.42666667rem 0rem 0.42666667rem 0rem;
  font-size: 0.32rem;
  font-weight: 200;
  color: #07111b;
  line-height: 0.42666667rem;
}
.activity li span {
  height: 0.42666667rem;
  width: 0.42666667rem;
  margin: 0rem 0.16rem 0rem 0rem;
}
.live-action {
  padding: 0.48rem 0rem 0.48rem 0.48rem;
  margin: 0.42666667rem 0rem 0rem 0rem;
  background-color: #fff;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.live-action .slide {
  width: 100%;
  margin: 0.32rem 0rem 0rem 0rem;
  overflow-x: hidden;
}
.live-action .slide ul {
  width: 750px;
}
.live-action .slide ul li {
  float: left;
  margin: 0rem 0.16rem 0rem 0rem;
}
.sellermsg {
  background-color: #fff;
  padding: 0.48rem 0rem 0.48rem 0.48rem;
  margin: 0.42666667rem 0rem 0rem 0rem;
}
.sellermsg h2 {
  margin: 0rem 0rem 0.32rem 0rem;
}
.sellermsg li {
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  height: 0.42666667rem;
  padding: 0.42666667rem 0rem 0.42666667rem 0.32rem;
  font-size: 0.32rem;
  font-weight: 200;
  color: #07111b;
  line-height: 0.42666667rem;
}



